@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.25rem;
  margin: 1rem 0;
  background-color: #4b4b4b;
}

.expense-item__description{
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2{
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin:  1rem;
  color: #fff;
}

.expense-item__price{
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background-color: #40005d;
  border: 1px solid #fff;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expense-item__description{
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }
  
  .expense-item_description h2{
    font-size: 1.25rem;
  }

  .expense-item__price{
    font-size: 1.25;
    padding: 0.5rem 1.5rem;
  }
}



.expense-date{
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #2a2a2a;
  color: #fff;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
}

.expense-date__month{
  font-weight: bold;
  font-size: 0.75rem;
}

.expense-date__year{
  font-size: 0.75rem;
}

.expense-date__day{
  font-weight: bold;
  font-size: 1.5rem;
}
.card{
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.expenses-container{
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  color: #fff;
  font-size: 1.2rem;
}
.new-expense{
  background-color: #a892ee;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25); 
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 1rem  2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: #fff;
}

.new-expense button:hover,
.new-expense button:active{
  background-color: #510674;
  border-color: #510674;
}

.new-expense button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active{
  background-color: #ddb3f8;
}





.new_expense__constrols{
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new_expense__constrol label{
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new_expense__constrol input {
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new_expense__actions {
  text-align: right;
}
.filter-container{
  display: flex;
  justify-content: space-between;
  background-color: #272627;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgb(0 0 0 / 25%);
  color: #fff;
  font-weight: 800;
  font-size: 1.2rem;
}

#year{
  border-radius: 12px;
  padding: 0 50px;
  font-size: 1.2rem;
  background-color: #fff;
}

#year:focus{
  border: none;
}
.chart {
  padding: 1rem;
  border-radius: 12px;
  background-color: #f8dfff;
  text-align: center;
  display: flex;
  justify-content: space-around;
  height: 10rem;
  width: 50%;
    margin: auto;
}
.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}
