.new_expense__constrols{
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new_expense__constrol label{
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new_expense__constrol input {
  padding: 0.5rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new_expense__actions {
  text-align: right;
}