.expense-date{
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 1px solid #ececec;
  background-color: #2a2a2a;
  color: #fff;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
}

.expense-date__month{
  font-weight: bold;
  font-size: 0.75rem;
}

.expense-date__year{
  font-size: 0.75rem;
}

.expense-date__day{
  font-weight: bold;
  font-size: 1.5rem;
}